/* üåô Îã§ÌÅ¨ Î™®Îìú - ÏôÑÏ†Ñ Í≤ÄÏ†ï Î∞∞Í≤Ω */
body {
    margin: 0;
    font-family: 'Merriweather', serif;
    background-color: #292929;
    color: #ffffff;
    transition: background 0.3s, color 0.3s;
}

/* üìö Ìó§Îçî */
.App-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #292929;
    padding: 20px 40px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
    position: relative;
}

/* üìå Ìó§Îçî ÏôºÏ™Ω: Î°úÍ≥† + Î©îÎâ¥ Î≤ÑÌäº */
.header-left {
    display: flex;
    align-items: center;
    position: relative;
}

.header-left h1 {
    font-size: 2em;
    margin: 0;
    color: #ffcc66;
}

/* ‚úÖ Ìó§Îçî Î©îÎâ¥ Î≤ÑÌäº (‚â°) */
.header-menu-btn {
    background: none;
    border: none;
    font-size: 1.8em;
    color: #ffcc66;
    margin-right: 15px;
    cursor: pointer;
    transition: color 0.3s;
}

.header-menu-btn:hover {
    color: #ffaa33;
}

/* ‚úÖ ÎìúÎ°≠Îã§Ïö¥ Î©îÎâ¥ Ïä§ÌÉÄÏùº */
.header-dropdown {
    position: absolute;
    left: 0;
    top: 50px;
    background: #2e2e2e;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    width: 150px;
    opacity: 0;
    transform: scale(0.95);
    transition: opacity 0.2s ease, transform 0.2s ease;
    color: #ffcc66;
}

.header-dropdown.show {
    opacity: 1;
    transform: scale(1);
}

.header-dropdown a {
    padding: 10px 15px;
    text-decoration: none;
    color: #ffdd95;
    font-size: 1em;
    display: block;
}

.header-dropdown a:hover {
    background: #444;
}

/* üìå Ìó§Îçî Ïò§Î•∏Ï™Ω: Î°úÍ∑∏Ïù∏, ÎßàÏù¥ÌéòÏù¥ÏßÄ, Î™®Îìú Ï†ÑÌôò Î≤ÑÌäº */
.header-nav { 
    display: flex;
    align-items: center;
    color: #ffdd95;
}

