.header-nav a {
    display: inline-block;
    color: #ffcc66;
    background-color: #333;
    /* Î≤ÑÌäº Î∞∞Í≤ΩÏÉâ */
    text-decoration: none;
    margin-right: 20px;
    font-size: 1.5em;
    font-weight: bold;
    padding: 5px 10px;
    /* Î≤ÑÌäº ÌÅ¨Í∏∞ Ï°∞Ï†à */
    border-radius: 10px;
    /* Îë•Í∑º ÎÑ§Î™® Î™®Ïñë */
    border: 2px solid #ffcc66;
    /* ÌÖåÎëêÎ¶¨ */
    transition: all 0.3s ease;
}

.header-nav a:hover {
    color: #fff;
    background-color: #ffaa33;
    border-color: #ffaa33;
}

/* ‚úÖ Îã§ÌÅ¨ Î™®Îìú ÌÜ†Í∏Ä Î≤ÑÌäº */
.toggle-btn {
    background-color: #444;
    color: #ffcc66;
    border: none;
    padding: 8px 15px;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s;
}

.toggle-btn:hover {
    background-color: #666;
}


a:visited , a {
    color: inherit !important;
    text-decoration: none;
}