/* ‚úÖ Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄÎ•º Í∞êÏã∏Îäî Ïª®ÌÖåÏù¥ÎÑà (Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨) */
.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    max-width: 600px;
    /* ‚úÖ Í∏∞Ï°¥Î≥¥Îã§ Ï¢åÏö∞Î°ú ÌôïÏû• */
    margin: 0 auto;
    /* ‚úÖ Ï§ëÏïô Ï†ïÎ†¨ Ïú†ÏßÄ */
}

/* ‚úÖ ÌöåÏõêÍ∞ÄÏûÖ Î∞ïÏä§ (Í∞ÄÎ°ú ÌÅ¨Í∏∞ Ï°∞Ï†ï) */
.signup-box {
    width: 500px;
    /* ‚úÖ Í∏∞Ï°¥ 400px ‚Üí 500pxÎ°ú ÌôïÏû• */
    padding: 25px;
    border-radius: 8px;
    text-align: center;
    transition: background 0.3s, box-shadow 0.3s, color 0.3s;
}

/* ‚úÖ ÏûÖÎ†• ÌïÑÎìú & Î≤ÑÌäº ÎÇòÎûÄÌûà Î∞∞Ïπò */
.input-flex {
    display: flex;
    gap: 10px;
    width: 100%;
}

/* ‚úÖ Î™®Îì† ÏûÖÎ†• ÌïÑÎìú ÌÅ¨Í∏∞ ÌÜµÏùº */
.input-group input {
    width: 100%;
    padding: 14px;
    /* ‚úÖ Í∏∞Ï°¥ 12px ‚Üí 14pxÎ°ú ÏÇ¥Ïßù ÌÇ§ÏõÄ */
    border: 1px solid #ffcc66;
    border-radius: 5px;
    font-size: 1em;
    box-sizing: border-box;
}

/* ‚úÖ Îã§ÌÅ¨ Î™®Îìú Ïä§ÌÉÄÏùº */
.dark-mode .signup-box {
    background: #1b1b1b;
    box-shadow: 0 5px 15px rgba(255, 204, 102, 0.2);
    color: #ffffff;
}

.dark-mode .input-group input {
    background: #292929;
    color: #ffffff;
    border: 1px solid #ffcc66;
}

.dark-mode .input-group input::placeholder {
    color: #cccccc;
}

/* ‚úÖ ÎùºÏù¥Ìä∏ Î™®Îìú Ïä§ÌÉÄÏùº */
.light-mode .signup-box {
    background: #fffaf0;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    color: #333333;
}

.light-mode .input-group input {
    background: #f2e6d9;
    color: #333333;
    border: 1px solid #4a2d00;
}

.light-mode .input-group input::placeholder {
    color: #666666;
}

/* ‚úÖ Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.verify-btn,
.check-btn,
.signup-btn {
    padding: 14px;
    /* ‚úÖ Î≤ÑÌäº ÌÅ¨Í∏∞ÎèÑ ÏûÖÎ†• ÌïÑÎìúÏôÄ ÎèôÏùºÌïòÍ≤å ÌÇ§ÏõÄ */
    border-radius: 5px;
    font-size: 1em;
    font-weight: bold;
    cursor: pointer;
    transition: background 0.3s, color 0.3s;
}

/* ‚úÖ ÎπÑÌôúÏÑ±ÌôîÎêú ÌöåÏõêÍ∞ÄÏûÖ Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.signup-btn:disabled {
    background: #999;
    color: #ccc;
    cursor: not-allowed;
}

.spinner {
    border: 8px solid #f3f3f3;
    border-top: 8px solid #3498db;
    /* Ïä§ÌîºÎÑà ÏÉâÏÉÅ */
    border-radius: 50%;
    width: 60px;
    height: 60px;
    animation: spin 1s linear infinite;
    margin: 100px auto;
    /* Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨ */
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}